@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
*{
    margin:0;
    padding:0;
    font-family: 'Josefin Sans', sans-serif;
    overflow:hidden;
}

.main_container{
    height:100vh;
    width:100%;
    background-color: rgb(240, 212, 232);
    display: flex;
    justify-content:center;
    align-items:center;
}
.appname{
    display: flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
}
.fa-hotel{
    font-size: 80px;
    margin-bottom:10px;
}
.lsbox{
    background-color: rgb(98, 0, 69);
    border-radius: 10px;
    color: white;
}

.entrybtns{
    display:flex;
    justify-content:center;
    align-items:center;
}

#logsign{
    background-color: white;
    width: 100px;
    height: 42px;
    border-radius: 3px;
    margin-right: 10px;
    border: none;
}


.dashbd{
    height:100vh;
    width:100%;
    background-color: rgb(3, 154, 241);
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
}

.Nav{
    width:100%;
    height: 60px;
    display:flex;
    text-align:center;
    justify-content:space-between;
    padding: 20px;
    align-items: center;
    color:white;
    background-color:rgb(98, 0, 69);
}
#logoutbtn{
    background:white;
    height: 30px;
    width:100px;
    border-radius:5px;
    text-decoration:none ;
    margin-right: 10px;
    margin-left: 10px;
    border:none;
}

.dash
{
    width:100vw;
    height:100vh;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content:center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
}

.blocks{
    background-color:rgb(42, 15, 34);
    height: 180px;
    width: 250px;
    border-radius:5px;
    display:flex;
    margin: 15px;
    justify-content:center;
    align-items: center;
    cursor:pointer;
    color:white;
    box-shadow: 8px 10px 10px rgb(85, 84, 84);
}

#homeblocks{
    color:white;
    font-weight:bold;
    font-size:20px;
    text-decoration:none;
}
.leftpanel{
    background-color: rgba(98, 0, 69, 0.657);
    height:100vh;
    width: 300px;
    display:flex;
    flex-direction: column;
    align-items: center;
    margin-left:-25px;
    position: relative;
    left: -10px;
}

.propic{
    height:100px;
    width: 100px;
    margin-top: 60px;
    margin-bottom: 20px;
    border:5px solid rgba(98, 0, 69);
    background-color: white;
    border-radius: 50%;
    display:flex;
    justify-content: center;
    align-items: center;
}

#userid{
    color: white;
    margin: 10px;
}
.rightpanel{
    height: 100vh;
    width:70vw;
    display:flex;
    justify-content:center;
    align-items: center;
    flex-wrap: wrap;
}

.Header{
    display:flex;
    justify-content:space-between;
    width: 100vw;
    height: 10vh;
    margin-top:20px;
    border-bottom: 2px solid black;
}
.bodypart{
    width: 100vw;
    height: 100vh;
    display:flex;
    align-items: center;
    flex-direction: column;
    margin-top:25px;
}
.id_room{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 20px;
}
#CHNG{
    width: 120px;
    height:30px;
    background-color:rgb(0, 0, 0);
    text-decoration: none;
    color: white;
    border-radius: 5px;
    display:flex;
    justify-content:center;
    align-items:center;

}

#finalbook{
    border: none;
}
#backbtn{
    width: 60px;
    height: 30px;
    color: white;
    display: block;
    padding: 5px;
    border-radius:5px;
    text-decoration: none;
    background-color: rgba(98, 0, 69);
}
#genders{
    width: 300px;
    height: 300px;
    display:flex;
}
#gen{
    width: 150px;
    height: 150px;
    background-color: white;
    border: none;
}
#gen{
    font-size: 100px;
    background-color: white;
}

#ico{
    font-size: 50px;
    margin-left: 10px;
    margin-right:10px;
    color:brown
}
#gen:hover{
    font-size: 150px;
    color: rgba(98, 0, 69);
}

.fa-exclamation-triangle{
    font-size: 80px;
    color:rgb(179, 0, 0);
}

.hostelbox{
    display: flex;
    flex-direction: column;
    justify-content:space-between;
    align-items: center;

}
.hostel_header{
    display:flex;
    margin-bottom: 50px;
}

.blocks_col{
    display: flex;
    justify-content:center;
    align-items: center;
    flex-wrap: wrap;
}
#blkbtn{
    width:150px;
    height:50px;
    background-color: rgb(51, 1, 36);
    color: white;
    border-radius:5px;
    margin:5px;
}

#roomid{
    width:80px;
    height:30px;
    border-radius:5px;
    margin:2px;
    background-color:rgb(170, 255, 0);
}
#roomid2{
    width:80px;
    height:30px;
    border-radius:5px;
    margin:2px;
    color:white;
    background-color:rgb(255, 60, 0);
}
#rids
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: white;
    margin-top:10px;
    border: 1px solid rgb(186, 248, 186);
    width: 100%;
    height: 200px;
}

/* Popup style */

.pbox{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#cnfrmbtn{
    background-color:rgb(15, 187, 15);
    color:white;
    display:block;
    width:100px;
    height:32px;
    border-radius:10px;
    text-decoration: none;
    padding:6px;
}

.cnfr{
    font-size: 50px;
    color: rgb(5, 200, 5);
    margin: 10px;
}
.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
   
  .box {
    position: relative;
    width: 50%;
    margin: 0 auto;
    height: 35%;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: #fff;
    border-radius: 4px;
    padding: 20px;
    border: 1px solid #999;
    overflow: auto;
  }
   
  .close-icon {
    content: 'x';
    color:white;
    font-size: 25px;
    cursor: pointer;
    position: fixed;
    background: #ededed;
    width: 25px;
    height: 25px;
    top:230px;
    left:72vw;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
    background-color: rgb(219, 4, 4);
  }

  
@media (max-width: 920px) 
{
    #logoutbtn{
        background:white;
        height: 20px;
        width:60px;
        margin-right: 5px;
        margin-left: 5px;
        border:none;
        font-size:14px;
    }
    .dash{
        flex-direction: row;
        overflow:scroll;
    }
    .leftpanel{
        height:100px;
        width: 100%;
        display:flex;
        flex-direction: row;
        margin-left:0px;
    }
    
.propic{
    height:60px;
    width: 60px;
    margin-top: 0px;
    margin-bottom: 0px;
   margin-left: 50px;
   margin-right: 10px;
}
#mailidd{
    display: none;
}
.rightpanel{
    width:100%;
    margin-left: 20px;
    margin-right: 20px;
}

.dash{
    overflow:scroll;
    width:100%;
}
.blocks{
    height: 100px;
    width: 90%;
    border-radius:5px;
    display:flex;
    margin: 5px;
    justify-content: space-evenly;
}
}
